const swiper = new Swiper('.swiper', {
    // Optional parameters
    // loop: true,
    // loopPreventsSliding: true,
    slidesPerView: "auto",
    spaceBetween: 40,
    autoHeight: true,
    centeredSlides: true,
    rewind: true,
    centerInsufficientSlides: true,
    cssMode: true,


    // // If we need pagination
    // pagination: {
    //     el: '.swiper-pagination',
    //     clickable: true,
    // },

    // Navigation arrows
    navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev',
    },

    // And if we need scrollbar
    scrollbar: {
        el: '.swiper-scrollbar',
    },
});